.weather-widget {
    font-family: 'Rubik', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
}
  
.search-container {
    display: flex;
    justify-content: left;
    align-items: center;
    margin-left: 25px;
    margin-bottom: 10px;
    margin-top: 10px;
    width: 100%;
}
  
.search-container input {
    width: 20%;
    padding: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
    border: 2px solid #ccc;
    border-radius: 8px;
    font-size: medium;
    font-weight: 400;
    color: #333333;
}
  
.status-container {
    margin-left: 10px;
}
  
.error {
    color: red;
}
  
.loading {
    color: #666;
}
  
.city-title {
    align-self: flex-start;
    font-size: medium;
    font-weight: 600;
    color: #333333;
    padding-bottom: 10px;
}
  
.weather-data {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-left: 25px;
    border-radius: 8px;
}
  
.weather-day {
    background-color: #f0f0f0;
    padding: 10px;
    margin: 10px 0;
    border-radius: 8px;
    width: 100%;
}

h3{
    font-size: medium;
    font-weight: 600;
    color: #333333;
    margin: auto;
    padding-bottom: 10px;
}
  
.hourly-data {
    display: flex;
    overflow-x: auto;
}
  
.hourly-item {
    background-color: #e0e0e0;
    padding: 10px;
    margin-right: 10px;
    border-radius: 8px;
    text-align: center;
    min-width: 120px;
}
  
.hour-time {
    font-weight:500;
    color: #333333;
}

.temperature{
    margin-top: 5px;
    font-size: medium;
    font-weight: 600;
    color: #333333;
}

.weather-description{
    color: #333333
}